const tableColumns = [
  {
    title: 'Location',
    field: 'title',
    isIcon: false,
    icon: ''
  },
  {
    title: 'Created At',
    field: 'createdAt',
    isIcon: false,
    icon: ''
  },
  {
    title: 'District',
    field: 'district',
    isIcon: false,
    icon: ''
  }
]

const tableActions = [
  {
    tag: 'button',
    class: 'btn btn-info',
    label: 'Generate Qr',
    dynamic: true,
    urlType: '_id',
    emit: 'generateQr'
  },
  {
    tag: 'link',
    class: 'btn btn-info',
    label: 'Edit',
    dynamic: false,
    to: '/locations/update',
    urlType: '_id',
    emit: ''
  },
  {
    tag: 'button',
    class: 'btn btn-danger',
    label: 'Remove',
    dynamic: false,
    urlType: '_id',
    to: '',
    emit: 'clickDelete'
  }
]

const types = [
  {
    name: 'anatomy',
    value: 'anatomy'
  },
  {
    name: 'speciality',
    value: 'speciality'
  }
]

const fields = [
  {
    name: 'title',
    label: 'Location title',
    placeholder: 'Location title',
    onChange: 'handleSetLocation',
    value: 'title',
    type: 'fg-input'
  },
  {
    name: 'ch_title',
    label: 'Location title on chinese',
    placeholder: 'Location title on chinese',
    onChange: 'handleSetLocation',
    value: 'ch_title',
    type: 'fg-input'
  },
  {
    name: 'phone',
    label: 'Phone number',
    placeholder: 'Phone number',
    onChange: 'handleSetLocation',
    value: 'phone',
    type: 'fg-input'
  },
  {
    name: 'website',
    label: 'Website',
    placeholder: 'Website',
    onChange: 'handleSetLocation',
    value: 'website',
    type: 'fg-input'
  },
  {
    name: 'dianping_id',
    label: 'Dianping Id',
    placeholder: 'Dianping',
    onChange: 'handleSetLocation',
    value: 'dianping_id',
    type: 'fg-input'
  },
  {
    name: 'average',
    label: 'Average',
    placeholder: 'Average',
    onChange: 'handleSetLocation',
    value: 'average',
    type: 'fg-input'
  },
  {
    name: 'category',
    label: 'Category',
    placeholder: 'Select categories',
    onChange: 'handleSetPlace',
    value: 'category',
    type: 'fg-input',
    showedKey: 'title'
  }
]

export {
  tableColumns,
  tableActions,
  types,
  fields
}



