<template>
  <div class="row">
    <div class="col-md-12">
      <alert 
        :alert="alert"
      />
      <div class="col-md-6 col-sm-6">
        <router-link 
          v-if="locations.length !== 0"
          class="btn btn-primary btn-link" 
          to="/locations/add"
        >
          Add new location
        </router-link>
      </div>
      <div class="col-md-6 col-sm-6">
        <fg-input
          @changeValue="globalSearch"
          placeholder="Search value"
          :value="searchValue"
        />
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="col-md-12">
      <div class="card" v-if="locations.length !== 0">
        <paper-table 
          :title="table.title" 
          :sub-title="table.subTitle" 
          :data="table.data" 
          :columns="table.columns"
          :actions="table.actions"
          :handleOrder="orderLocations"
          @clickDelete="handleDeleteLocation"
          @generateQr="id => generateQr({
            type: 'location',
            id
          })"
        />
        <button
          class="btn btn-primary pull-right newUserButton"
          @click="showMoreLocations"
          v-if="isMoreLocationAvialiable || count > 10"
        >
          Show more
        </button>
      </div>
      <jumbotron 
        title="Ups nothing"
        text="Please add more locations"
        v-if="locations.length === 0"
      >
        <router-link 
          class="btn btn-primary" 
          to="/locations/add"
        >
          Add new location
        </router-link>
      </jumbotron>
    </div>
  </div>
</template> 

<script>
  import { mapGetters, mapActions, mapState } from 'vuex'
  import { tableColumns, tableActions } from './TableMock.js'

  export default {
    components: {
      PaperTable: () => import('ui/PaperTable.vue')
    },
    data: () => ({
      tableActions,
      tableColumns,
      table: {
        title: 'Location list',
        subTitle: 'Location existing list',
        columns: [...tableColumns],
        actions: [...tableActions],
        data: []
      }
    }),
    mounted() {
      this.fetchLocations()
    },
    watch: {
      locations (value) {
        if (value) {
          this.table.data = [
            ...value
          ]
        }
      }
    },
    computed: {
      ...mapGetters({
        locations: 'getlocations',
        isMoreLocationAvialiable: 'isMoreLocationAvialiable'
      }),
      ...mapState({
        alert: state => state.alert,
        user: state => state.user,
        searchValue: ({ locations: { searchValue } }) => searchValue,
        count: ({ locations: { pagination } }) => pagination.count
      })
    },
    methods: {
      handleDeleteLocation(value) {
        const id = value
        this.removeLocation({ id })
      },
      ...mapActions([
        'fetchLocations',
        'removeLocation',
        'showMoreLocations',
        'globalSearch',
        'generateQr',
        'orderLocations'
      ])
    }
  }
</script>

<style lang="css" scoped>
  .newUserButton {
    margin: 10px 0px;
    display: block;
    width: 100%;
  }

  .alert {
    margin: 0px 0px 10px;
  }

  .btn-link {
    margin-bottom: 15px;
  }
</style> 



